.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__minutes-hand {
  transform-origin: 0;
  background-color: #0700ff;
  width: 20vmin;
  height: 3vmin;
  animation-name: move-clock;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  position: absolute;
  left: 40vmin;
  transform: rotate(270deg);
}

.stopwatch__minutes-hand--speed-up-1 {
  animation-duration: 1s;
}

